import styled from "styled-components"

export const WorkbenchHeaderContainer = styled.div`
  width: 100%;
  height: 30px;
  background-color: #0a101b;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .slider-box {
    width: 150px;
  }
`

export const ToolBtnComp = styled.div`
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
`

export const ToolBtnRightComp = styled.div`
  display: flex;
  width: 250px;
  height: 100%;
  justify-content: flex-end;
  align-items: center;
  user-select: none;
`

export const Button = styled.div`
  width: 45px;
  line-height: 30px;
  height: 30px;
  text-align: center;
  cursor: default;
  &:hover {
    background: #85b1d65c;
  }
`

export const OpenResourceListContainer = styled.div`
  height: 100%;
  width: 100%;
  line-height: 30px;
  background: #494a4c5e;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  &::-webkit-scrollbar {
    position: relative;
    width: 7px;
    height: 3px;
    background-color: rgb(45,45,45);
  }
  &::-webkit-scrollbar-thumb {
    background: #0a101b;
  }
  &:hover {
    overflow: auto hidden;
  }
`

export const FileList = styled.div`
  padding: 0 3px;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 14px;
  border-right: 1px solid rgb(37, 37, 38);
  background-color: rgb(45, 45, 45);
  color: rgba(255, 255, 255, 0.5);
  display: inline-block;
  min-width: 100px;
  max-width: 110px;
  user-select: none;
  cursor: pointer;
  box-sizing: border-box;
  &.select {
    color: #fff;
    background: #6d6d6d;
  }
  &:hover {
    color: #fff;
  }
`

export const EmptyText = styled.div`
  width: 100%;
  text-align: center;
  color: #fff;
`